import React from 'react';
import './pokemon-main-page.css'
import PokeballCircle from './PokeballCircle.js'
import Homepage from './libraries/homepage/Homepage.js'

class PokemonMainPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            logged: false,
        }
        this.handler = this.handler.bind(this);
    }
    handler(log) {
        this.setState({
            logged: log,
        })
    }
    render() {
        return (
            <div className={'main-page'}>
                {this.state.logged ? <Homepage logged={this.state.logged} /> : null}
                {this.state.logged ? null : <PokeballCircle handler={this.handler} logged={this.state.logged}/>}
            </div>
        )        
    }
}

export default PokemonMainPage;