import React from 'react';
import './pokemon-list.css'
import '../homepage/homepage.css'
import pokemonData from '../../data/pokemon-data.json'
import { motion } from "framer-motion";
import InfiniteScroll from 'react-infinite-scroller';
import Pokemon from '../pokemon/Pokemon.js'

class PokemonList extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            display: false
        }
        this.display = this.display.bind(this)
    }
    display() {
        this.setState({display : true});
    }
    render() {
        return(
            <div className={this.state.display ? 'contents-container display' : 'contents-container'}>
                <PokemonDatabase generate={this.state.display} pokemon={this.props.pokemon}/>
                <motion.div className={'tabs poke-database'} whileHover="hover" variants={preview}>
                    <div className={'hide-shadow'} />
                    <div className={'image-tab'} onClick={this.display}/>  
                </motion.div>
                <div className={'contents'}/>
            </div>
        )
    }
}

let isLoading = false;

class PokemonDatabase extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            maxPokemon: 0,
            pokemons: [],
            nextURL: pokemonData.url,
            hasMore: true,
        }
    }

    fetchPage = () => {
        const newPokes = [];
        fetch(this.state.nextURL)
            .then((res)=>{
                if (res.ok) {
                    return res.json()
                } else {
                    throw new Error('Something went wrong');
                }
            })
            .then((json)=>{
                json.results.forEach((obj) => {
                    newPokes.push({
                        "name": obj.name,
                        "url": obj.url,
                    })
                })
                
                if (json.next != null && json.next != "") {
                    this.setState({
                        maxPokemon: json.count,
                        nextURL: json.next,
                        hasMore: true,
                        pokemons: [...this.state.pokemons, ...newPokes]
                    })
                    console.log("Fetching another 20 pokemons...")
                } else {
                    this.setState({
                        nextURL: "",
                        hasMore: false,
                    })
                }
            })
            .catch((err)=> {
                console.log(err)
            })
    }
    render() {
        return (
            <div className={'pokedex-container'}>
                <div className={'pokedex-title-list'}>
                    {'Pokemon List'}
                </div>
                <div className={'pokedex-sorter'}>
                    <input className={'pokedex-search'} placeholder="Search pokemon..." type="text" />
                    <div className={'pokedex-sort-by'}>
                        <motion.div className={'sort-by'} whileHover="hover" variants={filterHover}>{"Name"}</motion.div>
                        <motion.div className={'sort-by'} whileHover="hover" variants={filterHover}>{"Index"}</motion.div>
                    </div>
                </div>
                <div className={'pokedex-type-filters'}>
                    {filters}
                </div>
                <div className={"pokedex-list scroller"} id={"pokedex-list"}>
                    <InfiniteScroll
                        pageStart={0}
                        loadMore={this.fetchPage}
                        hasMore={this.state.hasMore && this.props.generate}
                        useWindow={false}>
                        {this.state.pokemons.map((item, i)=>(  
                            <Pokemon key={i} id={i} name={item.name} url={item.url} />
                        ))}
                    </InfiniteScroll>
                </div>
            </div>
        )
    }
}

class Filter extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            active: false,
        }
    }
    render() {
        return (
            <div className={'filter-container'} style={{filter: `brightness(${this.state.active ? 1 : 0.7})`, background: this.props.color, boxShadow: `0 0 5px ${this.props.color}`}}>
                <img className={'filter-icon'} src={require(`../../assets/${this.props.text}.svg`)} />
            </div>
        )
    }
}


const filters = [];
for (const item in pokemonData["types"]) {
    filters.push(<Filter key={item} text={item} color={pokemonData["types"][item]}/>)
}

class Loader extends React.Component {
    render() {
        return (
            <div className={'loader-container'}>
                <div className={'loader-background'} />
                <div className={'loader-gif'} />
            </div>
        )
    }
}



const filterHover = {
    rest: {
        scale: 1,
    },
    hover: {
        scale: 1.1,
    }
}

const preview = {
    rest: {
        marginLeft: 0
    },
    hover: {
        marginLeft: "30px",
    }
}

export default PokemonList;


