import React from 'react';
import './pokemon-main-page.css';
import { motion } from "framer-motion";

class PokeballCircle extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            login:false,
            register:false,
            loggedIn:false,
        }
        this.handleSubmit = this.handleSubmit.bind(this)
        this.timer = null;
    }
    componentWillUnmount() {
        clearTimeout(this.timer)
    }
    handleSubmit(e) {
        e.preventDefault();
        this.setState({loggedIn:true})
        this.timer = setTimeout(() => {
            this.props.handler(true)
        }, 1000)
    }    
    render() {
        return (
            <div>
                <motion.div className={'main-panel-cluster login'} whileHover="" style={{background: this.state.login ? "#770b0b" : "#ee1515", animation: this.state.loggedIn ? 'main-panel-out 1.5s' : (this.state.login ? 'login-hover 0.5s' : 'login-unhover 0.5s')}} />
                <motion.div className={'main-panel-cluster register'} whileHover="" style={{background: this.state.register ? "#808080" : "#ffffff", animation: this.state.register ? 'register-hover 0.5s' : 'register-unhover 0.5s'}} />
                <motion.div className={'circle-container'} whileHover="">
                    <motion.div className={this.state.loggedIn ? 'top-circle-container out' : 'top-circle-container'} whileHover="hover" onHoverStart={e=>{this.setState({login:true})}} onHoverEnd={e=>{this.setState({login:false})}}>
                        <motion.div style={{position:"relative"}} transition={{duration: 0.4}} variants={topCircleMotion}>
                            <div className={'circle top'} />
                            <div className={"line top"}  />
                        </motion.div>
                        <div className={'form-boundary login'}>
                            <motion.form className={'menu-form login'} transition={{duration: 0.4}} variants={showForm} autoComplete="off" onSubmit={this.handleSubmit}>
                                <h3>{'Login'}</h3>
                                <hr />
                                <label className={'form-label'}>{'Username'}</label>
                                <input className={'menu-input'} type={'text'} value={'Please-Just-Press-OK-Auto-Filled'} required={true} />
                                <label className={'form-label'}>{'Password'}</label>
                                <input className={'menu-input'} type={'password'} value={'Please-Just-Press-OK-Auto-Filled'} required={true}/>
                                <button className={'form-button'}>{'OK'}</button>
                            </motion.form>
                        </div>
                    </motion.div>
                    <motion.div className={this.state.loggedIn ? 'bot-circle-container out' : 'bot-circle-container'} whileHover="hover" onHoverStart={e=>{this.setState({register:true})}} onHoverEnd={e=>{this.setState({register:false})}}>
                        <motion.div style={{position:"relative"}} transition={{duration: 0.4}} variants={botCircleMotion}>
                            <div className={'circle bot'} />
                            <div className={"line bot"}  />
                        </motion.div>
                        <div className={'form-boundary register'}>
                            <motion.form className={'menu-form register'} transition={{duration: 0.4}} variants={showForm} autoComplete="off">
                                <h3>{'Register'}</h3>
                                <hr />
                                <label className={'form-label'}>{'Username'}</label>
                                <input className={'menu-input'} type={'text'} value={'Please-Just-Press-OK-Auto-Filled'} required={true} />
                                <label className={'form-label'}>{'Password'}</label>
                                <input className={'menu-input'} type={'password'} value={'Please-Just-Press-OK-Auto-Filled'} required={true}/>
                                <button className={'form-button'}>{'OK'}</button>
                            </motion.form>
                        </div>
                    </motion.div>
                </motion.div>
            </div>
            
        )
    }
}

const topCircleMotion = {
    rest: { 
        top: "0vh",
    },
    hover: {
        top: "-100vh",
    }
};

const botCircleMotion = {
    rest: { 
        bottom: "0vh",
    },
    hover: {
        bottom: "-100vh",
    }
}

const showForm = {
    rest: {
        opacity: 0,
    },
    hover: {
        opacity: 1,
    }
}

export default PokeballCircle;