import React from 'react';
import './pokemon.css'
import { motion } from "framer-motion";
import dat from '../../data/pokemon-data.json'

import jsgradient from '../../../external-libraries/jsGradient.js'

class Pokemon extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fetched: false,
            showDetails: false,
            pokemonDetails: {
                types: [],
            },
        }
    }
    displayPokemonData = async () => {
        if (!this.state.fetched) {
            await fetch(this.props.url)
            .then((res)=>{
                if (res.ok) {
                    return res.json()
                } else {
                    throw new Error('Something went wrong');
                }
            })
            .then((json)=>{
                const details = {
                    name: json.name.charAt(0).toUpperCase(),
                    id: json.id,
                    image: json.sprites.other["official-artwork"].front_default, 
                    types: json.types.map((t) => {
                        return t.type.name;
                    }),
                    height: json.height,
                    weight: json.weight,
                    stats: json.stats,
                    abilities: json.abilities,
                    moves: json.moves,
                    catchsprite: json.sprites.front_default,
                };
                this.setState({showDetails: true, fetched: true, pokemonDetails: details})
            })
            .catch((err)=> {
                console.log(err)
            })
        } else {
            this.setState({showDetails: true})
        }
    }
    render() {
        return (
            <div>
                <motion.div 
                    className={"pokemon-name"}
                    whileHover={{scale: 1.3, cursor: 'pointer'}}
                    onClick={this.displayPokemonData}>
                        {`${this.props.name.toUpperCase()} #${(this.props.id < 10 ? `00${this.props.id}` : (this.props.id < 100 ? `0${this.props.id}` : `${this.props.id}`))}`}
                </motion.div>
                <PokemonDetails data={this.state.pokemonDetails} show={this.state.showDetails} />
            </div>
        )
    }
}

class PokemonDetails extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className={"pokemon-details"} style={{display: this.props.show ? "" : "none", backgroundImage: makeBackground(this.props.data.types)}}>
                <div style={{display: "flex"}}>
                    <div className={'pokemon-image'} style={{background: `url(${this.props.data.image}) 0px 0px / cover no-repeat`}}/>
                    <PokemonStats stats={this.props.data.stats} />
                </div>
                <div className={"type-container"}>
                    {this.props.data.types.map((type, i)=> {
                        return (
                            <div className={"pokemon-type-container"} style={{background: dat.types[type]}}>
                                <div className={"pokemon-type"} key={i}>{type.charAt(0).toUpperCase() + type.slice(1)}</div>
                            </div>
                        ) 
                    })}
                </div>
            </div>
        )
    }
}

class PokemonStats extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div className={'pokemon-stats'}>
                {this.props.stats != null ? 
                    this.props.stats.map((stat, i) =>{
                        console.log(stat)
                        return(
                            <div key={i} className={"pokemon-power"}>
                                <div className={'power-base'} style={{background: dat['power-palette'][stat.stat.name].base}} />
                                <div className={'power-fill'} style={{
                                    background: dat['power-palette'][stat.stat.name].fill,
                                    height: `${stat.base_stat / 252 * 100}%`}}>
                                    <div className={'fill-bar-blocker'} style={{background: dat['power-palette'][stat.stat.name].base}} />
                                </div>
                                <div className={'power-info'} style={{background: dat['power-palette'][stat.stat.name].base}}>
                                    {dat['power-palette'][stat.stat.name].name}
                                </div>
                            </div>
                        )
                    }) 
                    : ""
                }
            </div>            
        )
    }
}
