const newShade = (hexColor, magnitude) => {
    hexColor = hexColor.replace(`#`, ``);
    if (hexColor.length === 6) {
        const decimalColor = parseInt(hexColor, 16);
        let r = (decimalColor >> 16) + magnitude;
        r > 255 && (r = 255);
        r < 0 && (r = 0);
        let g = (decimalColor & 0x0000ff) + magnitude;
        g > 255 && (g = 255);
        g < 0 && (g = 0);
        let b = ((decimalColor >> 8) & 0x00ff) + magnitude;
        b > 255 && (b = 255);
        b < 0 && (b = 0);
        return `#${(g | (b << 8) | (r << 16)).toString(16)}`;
    } else {
        return hexColor;
    }
};

const makeBackground = (types) => {
    const colors = types.length == 1 ? [dat.types[types[0]], newShade(dat.types[types[0]], -100)] : ( types.length == 2 ? [dat.types[types[0]], dat.types[types[1]]] : [])
    if (colors.length == 0) return
    const grads = jsgradient.generateGradient(colors[0], colors[1], 4)
    return `linear-gradient(45deg, ${grads[0]}, ${grads[1]}, ${grads[2]}, ${grads[3]})`;   
}

export default Pokemon;